<template>
  <div class="group_list">
    <head-top title="小组列表"></head-top>
    <div class="main">
      <div class="item" v-for="(item, index) in groupList" :key="index" @click="lookGroup(item)">
        <div class="item_head">
          <span>{{item.groupName}}</span>
          <div>
            <van-button
              v-if="item.currentNum<item.groupMax"
              type="primary"
              size="mini"
              v-on:click.stop.prevent="joinGroup(item)"
            >加入</van-button>
            <van-button v-else type="danger" disabled size="mini">已满</van-button>
          </div>
        </div>
        <div class="item_foot">
          <span>小组人数：{{item.groupMax}}人</span>
          <span>已加入人数：{{item.currentNum}}人</span>
          <div></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import headTop from "@/components/headTop";
import { Button, Dialog } from "vant";

export default {
  name: "",
  components: {
    headTop,
    [Button.name]: Button,
    [Dialog.Component.name]: Dialog.Component
  },
  props: {},
  data() {
    return {
      groupList: []
    };
  },
  created() {
    this.getGroupDataList();
  },
  methods: {
    getGroupDataList() {
      this.$store
        .dispatch("GetGroupByUser")
        .then(res => {
          if (!res.Result && !res.IsGrouping) {
            this.$toast("当前尚无分组");
          } else if (res.Result) {
            if (res.Datas && res.Datas.length > 0) {
              this.groupList = [];
              res.Datas.forEach(item => {
                let groupItem = {
                  gatewayID: item.Key["GatewayID"],
                  groupID: item.Key["GroupID"],
                  groupName: item.Key["GroupName"],
                  groupMax: item.Key["GroupNumber"],
                  groupingID: item.Key["GroupingID"],
                  studentID: item.Key["StudentID"],
                  currentNum: item.Total,
                  gatewayIP: item["GatewayIP"]
                };
                this.groupList.push(groupItem);
              });
            } else if (res.Data) {
              if (this.$store.getters.groupID) {
                this.$router.push("/groupInfo");
              }
            }
          }
        })
        .catch(err => {
          this.groupList = [];
        });
    },
    lookGroup(item) {
      this.$router.push("/groupInfo/" + item.groupID);
    },
    joinGroup(item) {
      let groupInfo = item;
      Dialog.confirm({
        title: "加入小组",
        message: "是否加入组：" + item.groupName
      })
        .then(() => {
          this.$store
            .dispatch("JoinGroup", {
              groupID: groupInfo.groupID,
              group: groupInfo
            })
            .then(res => {
              if (res.Result) {
                this.$toast("加入成功");
                this.$router.push("/groupInfo");
              } else if (res.Info) {
                this.$toast(res.Info);
              }else{
                this.$toast("加入失败");
              }
            })
            .catch(err => {
              this.$toast("加入失败");
            });
        })
        .catch(() => {
          // on cancel
        });
    }
  }
};
</script>

<style lang='less' scoped>
.group_list {
  width: 100%;
  height: 100%;
  background: #fff;
  .main {
    .item {
      padding: 2vh 0;
      width: 92vw;
      border-bottom: 1px solid #dddddd;
      text-align: left;
      overflow: hidden;
      margin: 0 auto;

      .item_head {
        display: flex;
        justify-content: space-between;
        align-items: baseline;
        font-size: 1.675rem;
        line-height: 2;
        color: #333;
      }
      .item_foot {
        display: flex;
        justify-content: space-between;
        font-size: 1.25rem;
        line-height: 2;
        color: #666;
      }
    }
  }
}
</style>


